html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

/* Headings
----------------------------------------------------------------------------------------------------*/
html body,
html p {
  font-family: "maple-web", sans-serif;
}

h1 {
  line-height: 1.2;
  font-size: 25px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h1 {
  font-family: "maple-web", sans-serif;
}

h2 {
  line-height: 1.2;
  font-size: 23px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h2 {
  font-family: "maple-web", sans-serif;
}

h3 {
  line-height: 1.2;
  font-size: 21px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h3 {
  font-family: "maple-web", sans-serif;
}

h4 {
  line-height: 1.2;
  font-size: 19px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h4 {
  font-family: "maple-web", sans-serif;
}

h5 {
  line-height: 1.2;
  font-size: 17px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h5 {
  font-family: "maple-web", sans-serif;
}

h6 {
  line-height: 1.2;
  font-size: 15px;
  font-family: Circular, sans-serif;
  font-weight: 900;
}

.fonts-loading h6 {
  font-family: "maple-web", sans-serif;
}

/* Header css
 ----------------------------------------------------------------------------------------------------*/
body header#header {
  position: relative;
  z-index: 9998;
  background: #000;
}

body header#header #logo.logo-image {
  max-width: 200px;
  margin-top: 0;
  margin-bottom: 0;
}

@media screen and (min-width: 48em) {
  body header#header #logo.logo-image {
    max-width: none;
  }
}

body header#header.menu-below-logo nav.main-nav {
  margin-bottom: 0;
}

body header#header.menu-below-logo nav.main-nav > ul > li {
  font-family: "maple-web", sans-serif;
  font-weight: 700;
}

/* Footer css
 ----------------------------------------------------------------------------------------------------*/
body footer#footer::after {
  clear: both;
  content: "";
  display: table;
}

body footer#footer .widget .shop {
  font-family: "maple-web", sans-serif;
}

body footer#footer .footer-container {
  padding-top: 30px;
  padding-bottom: 30px;
}

body footer#footer .mc4wp-form {
  margin-bottom: 50px;
  font-family: "maple-web", sans-serif;
  font-style: normal;
  text-align: center;
}

body footer#footer .mc4wp-form label {
  text-transform: uppercase;
  font-weight: 300;
}

body footer#footer .mc4wp-form input[type=submit] {
  color: #fff;
  border: 1px solid #fff;
  border-radius: 0;
}

body footer#footer .mc4wp-form input[type=submit]:hover, body footer#footer .mc4wp-form input[type=submit]:active, body footer#footer .mc4wp-form input[type=submit]:focus {
  background: #fff;
  color: #000;
}

body footer#footer .mc4wp-form input[type=text],
body footer#footer .mc4wp-form input[type=password],
body footer#footer .mc4wp-form input[type=url],
body footer#footer .mc4wp-form input[type=email] {
  font-family: "maple-web", sans-serif;
  font-style: normal;
  text-align: center;
  border: 1px solid #fff;
  background: #111111;
}

.footer_widget:first-of-type {
  float: none;
  max-width: 400px;
  margin: 0 auto 30px;
}

@media screen and (min-width: 37.5625em) {
  .footer_widget {
    float: left;
    width: 50%;
  }
}

@media screen and (min-width: 64em) {
  .footer_widget {
    width: 25%;
  }
}

.shop {
  text-align: center;
  font-style: normal;
}

@media screen and (min-width: 64em) {
  .shop {
    text-align: left;
  }
}

.shop__title {
  color: #fff;
  font-weight: 300;
}

.shop p {
  color: #A4A4A4;
}

.shop a {
  text-decoration: none;
  color: #A4A4A4;
}

.shop a:hover, .shop a:active, .shop a:focus {
  color: #fff;
  text-decoration: underline;
}

body .section-content p {
  font-style: normal;
}

body.show-nav {
  overflow: hidden;
}

.main-nav {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 140px 0 0;
  overflow: auto;
  background: rgba(0, 0, 0, 0.9);
  text-align: center;
  opacity: 0;
  transition: opacity .3s ease-in-out;
}

@media screen and (min-width: 48em) {
  .main-nav {
    padding: 15% 0 0;
  }
}

.nav-opened .main-nav {
  display: block;
  z-index: 9997;
}

.show-nav .main-nav {
  opacity: 1;
}

.main-nav ul, .main-nav li {
  padding: 0;
  list-style-type: none;
}

.main-nav .menu-item {
  line-height: 50px;
  font-size: 32px;
  font-weight: 900;
  font-family: "maple-web", sans-serif;
}

@media screen and (min-width: 48em) {
  .main-nav .menu-item {
    margin: 15px 0;
    line-height: 40px;
    font-size: 42px;
  }
}

.main-nav .menu-item a {
  color: #fff;
}

.main-nav .sub-menu .menu-item {
  margin: 5px 0;
  line-height: 24px;
  font-size: 20px;
}

@media screen and (min-width: 48em) {
  .main-nav .sub-menu .menu-item {
    line-height: 35px;
    font-size: 25px;
  }
}

.main-nav #mobile-menu {
  display: none;
}

.button-nav {
  padding: 0;
  width: 25px;
  height: 25px;
  border: none;
  border-radius: 0;
}

.button-nav:hover, .button-nav:active, .button-nav:focus {
  background: none;
}

.button-nav img {
  width: 100%;
  height: auto;
}

.button-nav__wrapper {
  position: absolute;
  z-index: 9999;
  top: 43px;
  left: 20px;
}

.admin-bar .button-nav__wrapper {
  top: 88px;
}

@media screen and (min-width: 48em) {
  .button-nav__wrapper {
    left: 40px;
  }
  .admin-bar .button-nav__wrapper {
    top: 72px;
  }
}

.nav-opened .button-nav--open {
  display: none;
}

.button-nav--close {
  display: none;
}

.nav-opened .button-nav--close {
  display: block;
}

.main-slider h1 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
  font-size: 45px !important;
}

.main-slider h2 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
  font-size: 45px !important;
}

.main-slider h3 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
  font-size: 45px !important;
}

.main-slider h4 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
  font-size: 45px !important;
}

.main-slider h5 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
  font-size: 45px !important;
}

.main-slider h6 {
  font-weight: 900 !important;
  font-family: "maple-web", sans-serif !important;
  font-size: 45px !important;
}

@media screen and (min-width: 48em) {
  .main-slider h1 {
    font-size: 84px !important;
  }
  .main-slider h2 {
    font-size: 84px !important;
  }
  .main-slider h3 {
    font-size: 84px !important;
  }
  .main-slider h4 {
    font-size: 84px !important;
  }
  .main-slider h5 {
    font-size: 84px !important;
  }
