    font-size: 84px !important;
  }
}

.base-tile {
  position: relative;
}

.base-tile--shadow:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0.3;
  width: 100%;
  height: 100%;
  z-index: 0;
  background: #000;
  transition: opacity .3s ease-in-out;
}

.base-tile--shadow:hover:after, .base-tile--shadow:active:after, .base-tile--shadow:focus:after {
  opacity: .6;
}

.base-tile .wpb_wrapper {
  z-index: 1;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.base-tile .wpb_wrapper a {
  padding-right: 20px;
  padding-left: 20px;
}

.base-tile h2 {
  color: #fff;
  font-size: 30px;
  font-weight: 300;
}

.base-tile h2 strong {
  color: #fff;
  font-weight: 900;
}

.base-tile h2 a {
  color: #fff;
}

.base-tile h2 a:hover, .base-tile h2 a:active, .base-tile h2 a:focus {
  color: #fff;
}

@media screen and (min-width: 64em) {
  .base-tile h2 {
    font-size: 45px;
  }
}

.base-tile--overlay .wpb_wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.base-tile--overlay .wpb_wrapper a {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

div.rtb-booking-form {
  margin: 40px 0;
}

div.rtb-booking-form::after {
  clear: both;
  content: "";
  display: table;
}

@media screen and (min-width: 64em) {
  div.rtb-booking-form fieldset {
    float: left;
    width: 48%;
  }
  div.rtb-booking-form fieldset:nth-child(odd) {
    float: right;
  }
}

div.rtb-booking-form legend {
  display: none;
  padding: 0;
  text-transform: uppercase;
}

div.rtb-booking-form label {
  font-weight: 300;
  text-transform: uppercase;
  color: #808080;
}

div.rtb-booking-form form input,
div.rtb-booking-form form select,
div.rtb-booking-form form textarea {
  border-color: #000;
}

div.rtb-booking-form form button[type=submit] {
  margin-top: 30px;
  color: #000;
  border: 1px solid #000;
  border-radius: 0;
}

div.rtb-booking-form form button[type=submit]:hover, div.rtb-booking-form form button[type=submit]:active, div.rtb-booking-form form button[type=submit]:focus {
  background: #000;
  color: #fff;
}

